@import "../../global.scss";

.portfolioList {
  font-size: 14px;
  margin: 0 20px;
  padding: 7px;
  border-radius: 10px;
  cursor: pointer;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;

  &.active {
    background-color: $mainColor;
    color: white;
  }
}

ul {
  text-align: center;
  padding: 0;
  list-style: none;
  display: flex;
}
