@import "../../global.scss";


.contact {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;

  .tito{
width: 100%;
display: inline-block;
text-align: center;
position: absolute;
margin-top: 20px;

h1 {
  font-size: 50px;
}
  }
}

.con {
  display: flex;

}
.thank {
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
  align-items: center;
 

  .shake {
    width: 600px;
    height: 600px;

    @include mobile {
display: none;
    }
  }

  h2 {
    font-size: 60px;
    
    @include mobile {
      font-size: 30px;
    }
  }
  p {
    font-size: 30px;
    @include mobile {
      font-size: 15px;
    }
  }

  a {
    color: black;
  font-size: 20px;
  color: $secColor;
  }
}
